* {
  margin: 0;
  padding: 0;
}
.body {
  width: 100%;
  position: relative;
  min-height: 500px;
}

/* Navbar */
.navbar-logo {
  width: 100px;
  margin-left: 20px;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 50vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: #dfa92e;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.nav-links:hover {
  color: white;
  transition: .4s;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}

.menu-btn {
  position: relative;
  justify-content: center;
  display: none;
  align-items: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  /* border: 3px solid white; */
}
.menu-btn_burger {
  height: 4px;
  width: 40px;
  background: #dfa92e;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before,
.menu-btn_burger::after {
  content: " ";
  position: absolute;
  width: 40px;
  height: 4px;
  background: #dfa92e;;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before {
  transform: translateY(-10px);
}
.menu-btn_burger::after {
  transform: translateY(10px);
}

/* Animation */
.menu-btn.open .menu-btn_burger {
  transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-btn_burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn_burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}





@media screen and (max-width: 960px) {
  .NavbarItems {
      position: relative;
  }

  .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 68px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
  }

  .nav-menu.active {
      background:black;
      left: 0;
      top: 68px;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
  }

  .nav-links {
      text-align: center;
      padding: 2rem;
      width: 82%;
      display: table;
  }
  
  .menu-btn {
    display: block;
    position: absolute;
    top: -17px;
    right: -79px;
    transform: translate(-100%, 60%);
    font-size: 2.8rem;
    cursor: pointer;
  }

  .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
  }

  button {
      display: none;
  }
